import React from 'react'
import './NewsCard.css'
import newsImg from '../../Images/slideImages/news-slide-2.jpg'

function NewsCard(props) {
    // const { title, url, urlToImage, author, description, publishedAt } = props.details;
    const { title, url, urlToImage, description } = props.details;
    return (
        <>
            <div className="cardContainer">
                <div className="cardImgContainer">
                    <img src={urlToImage ? urlToImage : newsImg} alt="NewsImage" />
                </div>
                <div className="cardDetailContainer">
                    <div>
                        <h2>{title ? title?.split(' ').slice(0, 5).join(' ') : 'Title'}...</h2>
                        <p>{description ? description.split(' ').slice(0, 12).join(' ') : 'Description'}...</p>
                    </div>
                    <a href={url ? url : 'abc'} target="_blank" rel="noopener noreferrer"> <button>Read More</button>
                    </a>
                </div>

            </div>
        </>
    )
}

export default NewsCard;

