import React from 'react'
import LoadingBar from 'react-top-loading-bar'
import './Navbar.css';
import { Link } from 'react-router-dom';
import logo from '../../Images/logo.png';
import menuBtn from '../../Images/menu.png';
import crossBtn from '../../Images/cross.png';
import { toggleNav } from '../../Store/NavToggleSlice';
import { useSelector, useDispatch } from 'react-redux';
import { setLoaderProgress } from '../../Store/LoaderValueSlice'

function Navbar() {
    const navToggleVal = useSelector(state => state.navToggle);
    const loadingProgress = useSelector(state => state.loaderValue);
    const Dispatch = useDispatch();

    return (
        <>
            <LoadingBar style={{ height: '3px' }} color='#f63536' progress={loadingProgress}
                onLoaderFinished={() => Dispatch(setLoaderProgress(0))} />
            <header>
                <img src={logo} id="navLogo" alt="logo" />
                <img src={(navToggleVal) ? menuBtn : crossBtn} id="hamburgerBtn" alt="menuBtn" onClick={() => Dispatch(toggleNav())} />
                <ul className={(navToggleVal) ? 'active' : ''}>
                    <li><Link to="/">Home</Link></li>
                    <li><Link to="/news">News</Link></li>
                    <li><Link to="/">About</Link></li>
                    <li><Link to="/">Services</Link></li>
                    <li><Link to="/">Contact Us</Link></li>
                </ul>
            </header>
        </>
    )
}

export default Navbar
