import { configureStore } from '@reduxjs/toolkit';
import navReducer from './NavToggleSlice';
import loaderValueReducer from './LoaderValueSlice';
import homeNewsReducer from './HomeNewsSlice';
import mainNewsReducer from './MainNewsSlice';

const newsStore = configureStore({
    reducer: {
        navToggle: navReducer,
        loaderValue: loaderValueReducer,
        homeNews: homeNewsReducer,
        mainNews: mainNewsReducer,
    }
})

export default newsStore;