import React from 'react'
import { Swiper, SwiperSlide } from "swiper/react";
import { Autoplay, Pagination, Navigation } from "swiper";
import slide1 from '../../Images/slideImages/news-slide-1.jpg'
import slide2 from '../../Images/slideImages/news-slide-2.jpg'
import slide3 from '../../Images/slideImages/news-slide-3.jpg'
import slide4 from '../../Images/slideImages/news-slide-4.jpg'

import './Slider.css'
import "swiper/css";
import "swiper/css/pagination";
import "swiper/css/navigation";

function Slider() {
  return (
    <>
      <Swiper
        spaceBetween={0}
        centeredSlides={true}
        autoplay={{
          delay: 2000,
          disableOnInteraction: false,
        }}
        pagination={{
          clickable: true,
        }}
        navigation={true}
        modules={[Autoplay, Pagination, Navigation]}
        className="mySwiper"
      >
        <SwiperSlide><img src={slide1} alt="" /></SwiperSlide>
        <SwiperSlide><img src={slide2} alt="" /></SwiperSlide>
        <SwiperSlide><img src={slide3} alt="" /></SwiperSlide>
        <SwiperSlide><img src={slide4} alt="" /></SwiperSlide>
      </Swiper>
    </>
  );
}

export default Slider;