import { createSlice } from '@reduxjs/toolkit';

const navToggleSlice = createSlice({
    name: 'navToggleSlice',
    initialState: true,
    reducers: {
        toggleNav(state, actions) {
            return !state;
        }
    }
})

export const { toggleNav } = navToggleSlice.actions;
export default navToggleSlice.reducer;
