import React, { useEffect } from 'react'
import NewsCard from '../../Components/NewsCard/NewsCard'
import Slider from '../../Components/Slider/Slider'
import { useSelector, useDispatch } from 'react-redux';
import { fetchNews } from '../../Store/HomeNewsSlice'

function Home() {
  const Dispatch = useDispatch();
  const newsData = useSelector(state => state.homeNews);
  useEffect(() => {
    Dispatch(fetchNews());
  }, [Dispatch])

  return (
    <>
      <Slider />
      <section>
        <h1 className='mainHeading'>Top <span>Headlines</span></h1>
        <div className="newsContainer">
          {newsData.map((data, ind) => <NewsCard key={ind} details={data} />)}
        </div>
      </section>
    </>
  )
}

export default Home
